#components-layout-demo-fixed-sider {
  width: 300px;
}
.ant-layout-sider {
  /* width: 250px !important;
  max-width: 250px !important; */
}
#components-layout-demo-fixed-sider .logo {
  height: 32px;
  margin: 16px;
  background: rgba(255, 255, 255, 0.2);
}
.site-layout-background {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
  grid-gap: 30px;
  margin-right: 20px;
}
.site-layout-background > div {
  /* min-width: 100%; */
  max-width: 500px;
}
.ant-layout-header {
  height: 64px;
  padding: 0px 30px !important;
  color: rgba(0, 0, 0, 0.85);
  line-height: 64px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background: #1e1e2b !important;
}
.site-layout .site-layout-background {
  background: #1e1e2b;
}

.ant-layout-sider {
  min-width: 0;
  background: #50248f !important;
  border: none !important;
  position: fixed !important;
  transition: all 0.1s !important;
  z-index: 10;
  height: 100vh;
}
.ant-menu-vertical .ant-menu-item::after,
.ant-menu-vertical-left .ant-menu-item::after,
.ant-menu-vertical-right .ant-menu-item::after,
.ant-menu-inline .ant-menu-item::after {
  border: none !important;
}
.ant-input-textarea-show-count::after {
  color: #fff !important;
}

.ant-menu-inline,
.ant-menu-vertical,
.ant-menu-vertical-left {
  border: none !important;
}

.custom-card-explainer-header {
  /* border: 2px solid yellow; */
  position: relative;
}
.custom-card-explainer-body {
  /* border: 2px solid red; */
  padding: 10px;
}
.custom-card-explainer-header-sticker {
  position: absolute;
  padding: 10px;
  top: -35px;
  left: 10px;
  min-width: 100px;
  text-align: center;
  border-radius: 10px;
}
.custom-card-explainer-header-sticker > span > h3 {
  color: #fff;
  font-size: 20px;
  font-weight: 900;
  padding: 0;
  margin: 0;
}
.custom-card-explainer-header-sticker > h3 {
  color: #fff;
  font-size: 20px;
  font-weight: 900;
  padding: 0;
  margin: 0;
  border-top: 2px solid #fff;
}

.custom-card-explainer-header-sticker > h3 ~ h3 {
  border: none;
}
.custom-card-explaier-cryptoname {
  /* border: 2px solid white; */
  text-align: right;
  font-size: 30px;
  color: #fff;
  float: right;
  padding: 10px;
}
.custom-card-explaier-cryptoname > h3 {
  color: #fff;
}

.ant-layout-sider-children {
  background: #1e1e2b !important;
}
@media screen and (max-width: 500px) {
  .ant-layout-header {
    display: flex;
    flex-direction: column-reverse;
    align-items: flex-end;
    /* margin-bottom: 50px; */
  }
  .site-layout-background {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    grid-gap: 10px;
    margin-right: 20px;
  }
  .site-layout-background > div {
    width: 100%;
  }
  .ant-layout-content {
    padding: 20px 0 !important;
  }
}

.custom-card-style {
  margin: 16px;
  text-align: left;
  cursor: pointer;
  /* border: 10px solid #fff !important; */
  background-color: #272a3e !important;
  min-height: 300px;
}

.custom-card-header {
  /* border: 2px solid red !important; */
  position: relative;
}
.custom-card-style-r1 {
  font-size: 20px;
  text-transform: uppercase;
  /* border: 2px solid green; */
  width: 50%;
  margin-left: 50%;
  padding: 0;
  /* text-align: right; */
}
.custom-card-style-r1 > h3 {
  color: #fff;
  margin: 0;
}
.custom-card-style-r2 {
  display: flex;
  /* border: 2px solid blue; */
  justify-content: space-between;
}
.card-top-box {
  /* border: 2px solid yellow; */
  position: absolute;
  color: #fff;
  font-weight: 600;
  text-align: center;
  top: -25px;
  min-width: 120px;
  padding: 0 2px;
  border-radius: 10px;
}

.card-top-box > h3 {
  font-weight: 800;
  font-size: 19px;
  color: #fff;
  border-top: 2px solid #fff;
}
.card-top-box > h3 ~ h3 {
  border: none;
}

.custom-card-style p {
  margin: 0;
  /* border: 2px solid black; */
}

.custom-card-body {
  /* border: 2px solid blue; */
  display: flex;
  align-items: center;
  margin-top: 10px;
  height: 175px;
}

.custom-card-body > div {
  flex-grow: 0.6;
  /* border: 2px solid yellow; */
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-evenly;
  align-items: flex-start;
}
.custom-card-body > div > p {
  display: flex;
  align-items: center;
  /* text-align: center; */
  color: #fff;
  font-weight: 600;
}
.custom-card-body > div > p > .purple-backs {
  background-color: #cb52e3;
  color: #fff;
  padding: 10px;
  border-radius: 20px;
  width: 70px;
  margin-right: 10px;
  text-align: center;
}
.custom-card-body > p {
  flex-grow: 0.4;
  color: #fff;
  border-left: 5px solid #cb52e3;
  padding-left: 5px;
  width: 55%;
  overflow: hidden;
  min-height: 120px;
  max-height: 100%;
  font-size: 14.3px;
}

.custom-card-footer {
  padding: 10px;
  /* border: 1px solid green; */
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.card-icon-style {
  font-size: 30px;
  color: #fff !important;
}

.custom-card-footer > div {
  /* border: 1px solid green; */
  display: flex;
  align-items: center;
  width: 100px;
}

@media screen and (max-width: 500px) {
  .custom-card-style-r1 {
    text-align: right;
  }
  .ant-layout-sider-zero-width-trigger {
    top: 0 !important;
  }
  .ant-layout-sider-children {
    background: transparent !important;
  }
}

.admin-dashboard-selector {
  text-align: center;
  font-size: 18px;
  margin: 10px 0;
  cursor: pointer;
  padding: 10px 0;
  transition: all 0.3s;
}

.admin-dashboard-selector:hover {
  background-color: #f9c74f;
}

